import React from 'react';
import { Box, Text, Button, CloseButton } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';

const CardHelpGame = () => {
  const navigate = useNavigate();

  const handleCloseHelp = () => {
    navigate('/');
    window.location.reload(); // Recargar la pgina al cerrar el componente
  };

  return (
    <Box
      position="fixed"
      top={0}
      left={0}
      bottom={0}
      right={0}
      bg="rgba(0, 0, 0, 0.8)" // Fondo oscuro con opacidad
      display="flex"
      justifyContent="center"
      alignItems="center"
      zIndex="modal" // Para estar por encima de otros elementos
      onClick={handleCloseHelp} // Cerrar al hacer clic en el fondo gris
    >
      <Box
        p={6}
        bg="white" // Fondo blanco
        color="gray.800"
        borderRadius="md"
        border="1px solid"
        borderColor="gray.200"
        maxW="90vw" // Ancho mximo del 90% del viewport
        maxH="90vh" // Alto mximo del 90% del viewport
        overflowY="auto" // Aadido para permitir desplazamiento vertical si el contenido excede el tamao del recuadro
        textAlign="center" // Centrar el contenido del cuadro
        onClick={(e) => e.stopPropagation()} // Evitar el cierre al hacer clic dentro del recuadro
      >
        <CloseButton
          position="absolute"
          right={4}
          top={4}
          onClick={handleCloseHelp}
        />
        <Text fontSize="xl" fontWeight="bold" mb={4}>
          Instrucciones
        </Text>
        <Text mb={4}>
          Imagina que sos parte de la tripulacin de cabina en un vuelo que se estrell en el bosque.<br/>
          Tendrs que tomar decisiones como equipo para navegar por el bosque, buscar agua, comida y refugio, y encontrar ayuda.<br/>
          Despus de cada decisin, se presentar la siguiente situacin basada en la decisin tomada anteriormente.<br/>
          Trabaja en equipo para tomar decisiones sabias y garantizar la supervivencia de todos los miembros de la tripulacin y los pasajeros.
        </Text>
        <Box mt={4}> {/* Contenedor para centrar el botn */}
          <Button onClick={handleCloseHelp} colorScheme="blue">
            Cerrar
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default CardHelpGame;
